html {
  background: #f1f1f1;
}

body {
  padding: 0;
  font-family: Azeret Mono, monospace;
}

@keyframes tailSwish {
  0% {
    rotate: none;
  }

  25% {
    rotate: -2deg;
  }

  27% {
    rotate: -28deg;
  }

  30% {
    rotate: -25deg;
  }

  36% {
    rotate: -25deg;
  }

  50% {
    rotate: -5deg;
  }

  75% {
    rotate: 10deg;
  }

  78% {
    rotate: 5deg;
  }

  98% {
    rotate: 5deg;
  }

  100% {
    rotate: none;
  }
}

@keyframes headLook {
  0% {
    rotate: none;
  }

  60% {
    rotate: none;
  }

  80% {
    rotate: -12deg;
  }

  100% {
    rotate: none;
  }
}

@keyframes bubbleFood {
  0% {
    rotate: none;
  }

  33% {
    margin-top: 1rem;
    rotate: 2deg;
  }

  50% {
    margin-top: 0;
  }

  67% {
    margin-top: .6rem;
    rotate: -2deg;
  }

  100% {
    rotate: none;
  }
}

@keyframes thots {
  0% {
    opacity: 0;
    top: 13rem;
    left: 13rem;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    top: 12rem;
    left: 10rem;
  }
}

@keyframes typing {
  0% {
    width: 0;
  }

  30% {
    width: 40%;
  }

  45% {
    width: 40%;
  }

  60% {
    width: 65%;
  }

  65% {
    width: 65%;
  }

  100% {
    width: 100%;
  }
}

@keyframes blinkCaret {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

h3 {
  color: #56eeb8;
}

html, body, .content, .columns.content, article.column, #whiskerStage {
